html {
  scroll-behavior: smooth;
}

body {
  color: #000;
  background-color: #fff;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

address {
  font-style: normal;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.button {
  cursor: pointer;
  background-color: #fff;
  border: 0;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 7px 14px;
  font-size: 8px;
  transition-duration: .25s;
  display: flex;
}

.button__span--arrow {
  padding-right: 15px;
}

.button__red {
  color: #fff;
  background-color: #d41342;
}

.button__red:hover, .button__red:focus {
  background-color: #c7133e;
  transform: translateY(-3px);
}

.button__red svg {
  fill: #fff;
}

.button__white {
  color: #d41342;
  background-color: #fff;
}

.button__white:hover, .button__white:focus {
  background-color: #ecebeb;
  transform: translateY(-3px);
}

.button__white svg {
  fill: #d41342;
}

.button__white, .button__red {
  line-height: 10px;
}

.button__arrow {
  width: 40px;
  height: 40px;
  background: #fff url("button-arrow.5a357a40.png") center no-repeat;
}

.button__menu {
  width: 190px;
  padding: 12px 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
}

.button__section {
  width: 176px;
  padding: 12px 0;
  font-weight: 700;
  line-height: 18px;
}

.button__section--medium {
  font-size: 14px;
  line-height: 18px;
}

.button__section--big {
  font-size: 16px;
  line-height: 21px;
}

@media screen and (min-width: 1280px) {
  .button__red--hero, .button__white--hero {
    padding: 12px 20px;
    font-size: 14px;
    line-height: 18px;
  }

  .button__menu {
    width: 160px;
  }

  .button__section--big {
    width: 200px;
  }
}

.header-nav__right {
  z-index: 4;
  align-items: center;
  display: flex;
}

.section-nav {
  background-color: #ffb8ca;
  justify-content: center;
  display: flex;
}

.center-nav {
  width: 100%;
  max-width: 480px;
  justify-content: space-between;
  align-items: center;
  padding: 4px 0 0 20px;
  display: flex;
}

.logo {
  cursor: pointer;
}

@media screen and (min-width: 1280px) {
  .center-nav {
    max-width: 1280px;
    padding: 37px 115px 0 46px;
  }
}

@media screen and (max-width: 1279px) and (min-width: 768px) {
  .center-nav {
    max-width: 768px;
    padding: 37px 34px 0;
  }
}

.main-header {
  align-items: center;
  display: flex;
}

.main-header__nav {
  z-index: 4;
  align-items: center;
  display: flex;
}

.main-header__link {
  color: #fff;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
  transition-duration: .25s;
  display: inline-block;
}

.main-header__link:hover {
  color: #d41443;
  transform: translateY(-2px);
}

.main-header__list {
  z-index: 4;
  gap: 35px;
  margin-left: 110px;
  display: flex;
}

@media (max-width: 767px) {
  .main-header__btn {
    display: none;
  }
}

@media screen and (max-width: 1279px) {
  .main-header__list {
    width: 1px;
    height: 1px;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    border: 0;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }
}

.hamburger {
  padding: 0 20px;
}

@media screen and (min-width: 1280px) {
  .hamburger {
    display: none;
  }
}

.menu-container {
  width: 100vw;
  height: 100vh;
  max-width: 250px;
  z-index: 999;
  background-color: #e17992;
  transition: transform .6s ease-out;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0%);
}

.mobile-menu {
  height: 100%;
  flex-direction: column;
  margin-top: 60px;
  display: flex;
}

.mobile-menu__button {
  justify-content: center;
  margin-bottom: 19px;
  display: flex;
}

.mobile-menu__link {
  color: #fff;
  margin-left: 21px;
  font-weight: 700;
  line-height: 1.28;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.mobile-menu__link--active {
  color: #d41443;
}

.mobile-menu__link:hover, .mobile-menu__link:focus {
  color: #d41443;
  outline: none;
}

.mobile-menu__border {
  border-top: 1px solid #e18298;
  padding: 13px 0;
}

.mobile-menu__border--bottom {
  border-bottom: 1px solid #e18298;
}

.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  fill: #d41443;
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (min-width: 1280px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle:hover, .menu-toggle:focus {
  fill: #c7133e;
}

.menu-container .menu-toggle {
  fill: #fff;
  position: absolute;
  top: 9px;
  right: 20px;
}

.menu-container .menu-toggle:hover, .menu-container .menu-toggle:focus {
  fill: #d41443;
}

.products {
  font-family: Titan One, sans-serif;
}

.products__header {
  color: #d41443;
  text-align: center;
  text-transform: uppercase;
  font-size: 34px;
  font-weight: inherit;
  margin-top: 15px;
  display: block;
}

.products__label {
  color: #ffa5ba;
  font-size: 18px;
  font-weight: inherit;
  text-align: center;
  text-transform: uppercase;
  margin-top: 121px;
  display: block;
}

.products__container {
  max-width: 280px;
  flex-flow: wrap;
  gap: 130px;
  margin: 144px auto 98px;
  display: flex;
}

.products__card {
  width: 100%;
  background-color: #ffb8ca;
  border-radius: 24px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

.products__card:hover, .products__card:hover .products__button {
  box-shadow: 0 5px 15px #90233ea1;
}

.products__card:hover .products__name, .products__card:hover .products__description {
  transform: scale(1.2);
}

.products__card--yellow {
  background-color: #f0d1a5;
}

.products__card--green {
  background-color: #c2e297;
}

.products__card:before {
  content: "";
  width: 249px;
  height: 252px;
  background-image: url("1_products_mb@1x.dc7b7783.webp");
  background-size: cover;
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -45%);
}

.products__card--yellow:before {
  background-image: url("2_products-mb@1x.45aab47b.webp");
}

.products__card--green:before {
  background-image: url("3_products-mb@1x.e2bc2a41.webp");
}

.products__name {
  color: #fff;
  font-size: 20px;
  font-weight: inherit;
  text-transform: uppercase;
  margin-top: 161px;
  transition: transform 1s cubic-bezier(.4, 0, .2, 1);
}

.products__name:after {
  content: "";
  width: 30px;
  border-bottom: 6px dotted #fff;
  margin: 30px auto auto;
  display: block;
}

.products__description {
  color: #fff;
  text-align: center;
  margin: 30px 40px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 176%;
  transition: transform 1s cubic-bezier(.4, 0, .2, 1);
}

.products__button {
  margin-bottom: 41px;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .products__card:before {
    background-image: url("1_products-mb@2x.4cb1aeac.webp");
  }

  .products__card--yellow:before {
    background-image: url("2_products-mb@2x.5145784f.webp");
  }

  .products__card--green:before {
    background-image: url("3_products-mb@2x.f0faef3d.webp");
  }
}

@media screen and (min-width: 768px) {
  .products__header {
    font-size: 48px;
  }

  .products__label {
    margin-top: 122px;
    font-size: 26px;
  }

  .products__container {
    max-width: none;
    flex-wrap: nowrap;
    justify-content: center;
    gap: 20px;
    margin: 150px auto 112px;
  }

  .products__card {
    width: 33%;
    max-width: 220px;
  }

  .products__card:before {
    width: 220px;
    height: 224px;
    background-image: url("1_products-tb@1x.7a1e7c4d.webp");
  }

  .products__card--yellow:before {
    background-image: url("2_products-tb@1x.7d717c96.webp");
  }

  .products__card--green:before {
    background-image: url("3_products-tb@1x.6f07fab1.webp");
  }

  .products__name {
    color: #fff;
    text-transform: uppercase;
    margin-top: 152px;
    font-size: 20px;
  }

  .products__description {
    margin: 30px 11px 40px;
  }

  .products__description .products_description-hidden {
    display: none;
  }

  .products__button {
    margin-bottom: 40px;
  }
}

@media (min-width: 768px) and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .products__card:before {
    background-image: url("1_products-tb@2x.46ea2dfc.webp");
  }

  .products__card--yellow:before {
    background-image: url("2_products-tb@2x.0c57957e.webp");
  }

  .products__card--green:before {
    background-image: url("3_products-tb@2x.3965b28b.webp");
  }
}

@media screen and (min-width: 1280px) {
