    font-size: 58px;
  }

  .products__label {
    margin-top: 127px;
    font-size: 30px;
  }

  .products__container {
    gap: 30px;
    margin: 259px auto 98px;
  }

  .products__card {
    width: 33%;
    max-width: 329px;
  }

  .products__card:before {
    width: 335px;
    height: 335px;
    background-image: url("1_products-dt@1x.f6e33f39.webp");
    transform: translate(-50%, -60%);
  }

  .products__card--yellow:before {
    background-image: url("2_products-dt@1x.a7703bff.webp");
  }

  .products__card--green:before {
    background-image: url("3_products-tb@1x.6f07fab1.webp");
  }

  .products__name {
