  }

  .products__button {
    margin-bottom: 50px;
  }
}

@media (min-width: 1280px) and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .products__card:before {
    background-image: url("1_products-dt@2x.f3e64ba6.webp");
  }

  .products__card--yellow:before {
    background-image: url("2_products-dt@2x.6b518c21.webp");
  }

  .products__card--green:before {
    background-image: url("3_products-dt@2x.99a4feb6.webp");
  }
}

